<template>
    <div>
        <RightTitle title="发信箱"></RightTitle>
        <MessageTab position="send"></MessageTab>

        <!-- 搜索表单 -->
        <div class="uar_form">
        <el-form ref="form" :inline="true" :model="filter" label-width="80px" size="medium">
            <el-form-item label="标题">
                <el-input v-model="filter.title" clearable placeholder="搜索标题"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
        </el-form>
        </div>

        <!-- 数据表格 -->
        <div class="uar_form">
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="title" label="标题"></el-table-column>
            <el-table-column prop="userNick" label="收件人" width="120"></el-table-column>
            <el-table-column prop="isRead" label="对方读取状态" width="120" align="center"></el-table-column>
            <el-table-column prop="addTime" label="发送时间" width="150" align="center"></el-table-column>
            <el-table-column label="详情" width="100" align="center">
            <template slot-scope="scope">
                <el-button @click="showInfo(scope.row)" type="text" size="small">详情</el-button>
            </template>
            </el-table-column>
        </el-table>
        </div>

        <!-- 分页 -->
        <div class="uar_form page_area">
        <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
        </div>

        <!-- 详情 -->
        <DialogMessageInfo :dialogVisible="dialogVisible" title="消息详情" @changeVisible="getDialogVisible"></DialogMessageInfo>
    </div>
</template>

<script>
import RightTitle from '../../components/RightTitle'
import MessageTab from '../../components/MessageTab'
import DialogMessageInfo from "../../components/DialogMessageInfo";
export default {
    name:"send",
    components:{
        RightTitle,
        MessageTab,
        DialogMessageInfo
    },
  data() {
    return {
      filter: {
        title: "", //标题搜索
      },
      tableData: [
        {
          title: "123456",
          userNick: "李伟",
          isRead: "是",
          addTime: "2019-09-26 11:22"
        }
      ],
      dialogVisible: false,
    };
  },
  methods: {
    showInfo(row) {
      console.log(row);
      this.dialogVisible = true;
    },
    getDialogVisible(data){
        this.dialogVisible = data
    }
  }
}
</script>

